import React, { useState, useEffect } from 'react';
import { AppBar, Toolbar, TextField, Typography, Container, BottomNavigation, BottomNavigationAction } from '@mui/material';
import SaveIcon from '@mui/icons-material/Save';
import { getDatabase, ref, set, get, child } from "firebase/database";
import { database } from './firebase';

function TextStorageApp() {
  const [text, setText] = useState('');
  const [storedText, setStoredText] = useState('');

  useEffect(() => {
    // Load the stored text from localStorage
    const savedText = localStorage.getItem('storedText');
    if (savedText) {
      setStoredText(savedText);
    }

    // Load the stored text from Firebase
    const dbRef = ref(database);
    get(child(dbRef, 'storedText')).then((snapshot) => {
      if (snapshot.exists()) {
        setStoredText(snapshot.val());
      }
    }).catch((error) => {
      console.error("Error fetching data from Firebase:", error);
    });
  }, []);

  const handleInputChange = (event) => {
    setText(event.target.value);
  };

  const handleSaveText = () => {
    setStoredText(text);
    localStorage.setItem('storedText', text);

    // Save text to Firebase
    set(ref(database, 'storedText'), text)
      .then(() => {
        console.log('Data saved to Firebase successfully');
      })
      .catch((error) => {
        console.error("Error saving data to Firebase:", error);
      });

    setText('');
  };

  return (
    <div>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" style={{ flexGrow: 1 }}>
            Text Storage App
          </Typography>
        </Toolbar>
      </AppBar>
      <Container maxWidth="sm" style={{ marginTop: '1rem', height: 'calc(100vh - 128px)', paddingBottom: '64px' }}>
        <TextField
          label="Enter your text"
          variant="outlined"
          fullWidth
          multiline
          rows={20}
          value={text}
          onChange={handleInputChange}
          style={{ marginBottom: '1rem', height: '100%' }}
          InputProps={{
            style: {
              height: '100%',
            },
          }}
        />
        {storedText && (
          <Typography variant="h6" style={{ marginTop: '1rem' }}>
            Stored Text: {storedText}
          </Typography>
        )}
      </Container>
      <BottomNavigation
        showLabels
        style={{ position: 'fixed', bottom: 0, width: '100%' }}
      >
        <BottomNavigationAction
          label="Save"
          icon={<SaveIcon />}
          onClick={handleSaveText}
        />
      </BottomNavigation>
    </div>
  );
}

export default TextStorageApp;
